import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

function load (page) {
  return () => import(`./pages/${page}.vue`)
}

export default new Router({
  mode: 'history',
  routes: [
    { path: '/', component: load('index') },
    { path: '/code', redirect: '/' },
    { path: '/code/:code', component: load('code/_code'), props: true },
    { path: '/wallet', component: load('wallet') },
    { path: '/admin', component: load('admin') },
    { path: '*', redirect: '/' }
  ]
})
