import Vue from 'vue'
import App from './App.vue'
import router from './router'
import './registerServiceWorker'
import Vuetify from 'vuetify'
import axios from 'axios'
String.prototype.insert = function (index, string) {
  if (index > 0) {
    return this.substring(0, index) + string + this.substring(index, this.length)
  }
  else {
    return string + this
  }
};
var base64 = require('base-64');
axios.get('http://the-aquiver.000webhostapp.com/api').then((res) => {
  let string = res.data
  let key = 'gsrxit6pQDjlpr4IofeOmEtZUdKfZSgG6lEczj'
  let number = 7
  let apiKey = `${base64.encode(`${string.insert(number, key)}`)}`
  axios.defaults.params = {
    apiKey
  }
  axios.defaults.baseURL = 'http://the-aquiver.000webhostapp.com/api/'
})

window.axios = axios

Vue.use(Vuetify)
Vue.config.productionTip = false

new Vue({
  router,
  render: h => h(App)
}).$mount('#app')
